            color="green darken-2"
            clearable
            solo
            single-line
            v-model="keyWord"
            @click:append="search(`/search`)"
            v-on:keyup.enter="search(`/search`)"
            class="xs-12"
            dense
            elevation-0
          ></v-text-field>

<v-tooltip bottom v-for="(item, i) in items"
          :key="i" >
