<script>

export default {
  name: 'App',

  data: () => ({
    marker: true,
    keyWord: '',
      fav: true,
      items: [
        {
          icon: 'mdi-store',
          text: 'ลงขาย',
          path: '/posting'
        },
        {
          icon: 'mdi-bell-outline',
          text: 'การแจ้งเตือน',
          path: '/notifications'
        },
        {
          icon: 'mdi-heart-outline',
          text: 'รายการโปรด',
          path: '/favorite'
        },
        {
          icon: 'mdi-cart-outline',
          text: 'รถเข็น',
          path: '/cart'
        },
        {
          icon: 'mdi-logout',
          text: 'ออกจากระบบ',
          path: '/logout'
        },
      ],
      model: 1,
  }),
  methods: {
      toggleMarker () {
        (this.marker===true)?this.$router.push({path: '/about'}):this.$router.push({path: '/'})
        this.marker = !this.marker
      },
      goTo(path) {
        this.$router.push({path: path}).catch(() => {})
    },
    search(path){
      if((this.keyWord.trim()!=='')&&(this.keyWord))
      {
        this.$router.push({path: path,query: { test: this.keyWord.trim() }}).catch(() => {})
        this.reloadPage()
      }
    },
    reloadPage(){
    window.location.reload()
  }
  }
}

</script>